import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
 const state={   //state
     showFooter: true,
     changableNum:0,
     count:0,
     //
   };
const getters = {   //state()
    isShow(state) {  //showFooter
       return state.showFooter
    },
    getChangedNum(){  //changebleNum
       return state.changableNum
    }
};
const mutations = {
    show(state) {   //statestate();
        state.showFooter = true;
    },
    hide(state) {  //
        state.showFooter = false;
    },
    newNum(state,sum){ //statesum
       state.changableNum+=sum;
    },
    increment (state) {
        state.count++
    }
};
 const actions = {
    hideFooter(context) {  //mutationscontextstore 
        context.commit('hide');
    },
    showFooter(context) {  //
        context.commit('show');
    },
    getNewNum(context,num){   //num
        context.commit('newNum',num)
     },
    checkout ({ commit, state }, products) {
    // 
    const savedCartItems = [...state.cart.added]
    // 
    commit(types.CHECKOUT_REQUEST)
    //  API 
    shop.buyProducts(
        products,
        // 
        () => commit(types.CHECKOUT_SUCCESS),
        // 
        () => commit(types.CHECKOUT_FAILURE, savedCartItems)
    )
    },
    actionA ({ commit }) {
        return new Promise((resolve, reject) => {
            setTimeout(() => {
            commit('someMutation')
            resolve()
            }, 1000)
        })
    }
};
  const store = new Vuex.Store({
       state,
       getters,
       mutations,
       actions
});
export default store;